import TeacherHeader from './components/header'
import TeacherList from './components/list'
import axios from 'axios'

export default {
  name: 'assistant',
  components: {
    TeacherHeader,
    TeacherList
  },
  data () {
    return {
      projectList: []
    }
  },
  methods: {
    getAllProject () {
      axios.get(this.url + '/project/all')
        .then(this.getAllProjectSucc)
    },
    getAllProjectSucc (res) {
      if (res.status !== 200) this.$router.push('./noProject')
      else if (res) {
        res = res.data
        this.projectList = res
      }
    },
    getLeaderProject () {
      axios.get(this.url + '/project/leader/' + sessionStorage.getItem('id'))
        .then(this.getLeaderProjectSucc)
    },
    getLeaderProjectSucc (res) {
      if (res) {
        res = res.data
        this.projectList = this.projectList.concat(res)
      }
    },
    getParticipantProject () {
      axios.get(this.url + '/project/participant/' + sessionStorage.getItem('id'))
        .then(this.getParticipantProjectSucc)
    },
    getParticipantProjectSucc (res) {
      if (res) {
        res = res.data
        this.projectList = this.projectList.concat(res)
      }
    }
  },
  mounted () {
    // let level = parseInt(localStorage.getItem('level'))
    // // let level = 6
    // if (level === 6) {
    //   this.getAllProject()
    // } else {
    this.getLeaderProject()
    this.getParticipantProject()
    // }
  }
}



